import { Component,    ViewChild } from '@angular/core';
import { Alert, AlertController, Nav, Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { HomePage } from '../pages/home/home';
import { User } from './../shared/user';
import { Observable } from 'rxjs';
import { LoadingController } from 'ionic-angular/components/loading/loading-controller';
import { UserProvider } from '../providers/user/user';
import { AngularFireStorage, AngularFireUploadTask } from '@angular/fire/storage';
import { AngularFirestore } from '@angular/fire/firestore';
import { AngularFireDatabase } from '@angular/fire/database';
import { AngularFireAuth } from '@angular/fire/auth';
@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;
  user = {} as User;


  selectedFile : any;

  filepath2 : any;
  emailError : any;
  note: any ;
  img1 : any;
  src : any;
  downloadableURL = '';                      
  task: AngularFireUploadTask;               
userid : any;
  progressValue: Observable<number>; 
  basePath = '/photoAlbum';
  inputFile : any;


rootPage: any = HomePage;
  

  pages: Array<{title: string, icon: string, component: any}>;

  constructor(private alertCtrl: AlertController, public platform: Platform, public statusBar: StatusBar, public splashScreen: SplashScreen,
    private db: AngularFirestore, private fireStorage: AngularFireStorage,private userservice: UserProvider,public loadingCtrl: LoadingController
    ,private db2: AngularFireDatabase,
    private afAuth: AngularFireAuth
    ) {
    this.initializeApp();

    // used for an example of ngFor and navigation  
    

  }

  initializeApp() {
    this.platform.ready().then(() => {
     
      this.statusBar.styleDefault();
      this.splashScreen.hide();
   
    });
  }



  chooseFile (event) {
if (event.target.files[0].size < 303110){

    if (event.target.files && event.target.files[0]) {
      this.selectedFile = event.target.files[0];
      let reader = new FileReader();
      reader.onload = (event:any) => {
        this.img1 = event.target.result;
      }
      reader.readAsDataURL(event.target.files[0]);
    }

  }else{

    let alert = this.alertCtrl.create({
      title: '<font color="green"><b> </b> The image should be less then 301KB </font>',
      cssClass: "alert",
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          handler: data => {
            this.selectedFile = "";
          }
        },
       
      ]
    });
    alert.present();


  this.selectedFile = "";
  }//end of else state,
   
  }

  async uploadFile (user : any) {

  if (this.selectedFile) {
  
  var loading = this.loadingCtrl.create({
    spinner: 'bubbles',
    dismissOnPageChange: true,
    content: 'Data uploading in progress <br> Please Wait...'
  });
  loading.present();
  
  const filePath = `${this.basePath}/${user.name.trim()}`;  
  this.filepath2 = filePath;// inorder to get the path to delete file
  this.task =  this.fireStorage.upload(filePath, this.selectedFile);    
  
  
  this.progressValue = this.task.percentageChanges();       // <<<<< Percentage of uploading is
  (await this.task).ref.getDownloadURL().then(url => {this.downloadableURL = url;
   
  user.photourl = this.downloadableURL;

    try {
     const result = this.afAuth.auth.createUserWithEmailAndPassword(user.email, user.phone);
    if(result)
         this.afAuth.authState.take(1).subscribe(auth =>{
           this.userid = auth.uid;

           this.db2.object('photoalbum/users/'+auth.uid).set(user)
           .then(()=>{
            this.afAuth.auth.signOut().then(()=>{

              let alert2 = this.alertCtrl.create({
                
                title: '<font color="green"><b> </b>Upload Successful Your unique ID = <font color="black">'+this.userid+'</font></font>',
                cssClass: "alert",
                buttons: [
                  {
                    text: 'Ok',
                    role: 'cancel',
                    handler: data => {
                    //  this.afAuth.auth.signOut().then(()=>{})
                    }
                  },
                 
                ]
              });
              alert2.present();
              loading.dismiss();
          
  
  
  


            });
             
            
           });
 
         });//end db.authstarte
     //  this.itemsRef.push({ name: user.name, email : user.email, admno: user.admno, department: user.department });
     //  this.afAuth.auth.signInWithEmailAndPassword(user.email, user.password);
    //   var  userg = firebase.auth().currentUser;
      // userg.updateProfile({
        // displayName: "Shanwili",
         //photoURL: "klslsls"
      // });
    
       
 } catch(error){ 
      this.emailError = error.message;
      alert(error.message);
      
     console.error(error);
     
    }
 
 
  //end of register 
 









  
  
  
  
  
  
  
  });///end of await  
  
  
  
  
  
  
  
  
     } else { 
       

      let alert1 = this.alertCtrl.create({
        title: '<font color="green"><b></b>No image selected, upload without image</font>',
        cssClass: "alert",
        buttons: [
          {
            text: 'NO',
            role: 'cancel',
            handler: data => {
  
            }
          },
          {
            text: 'Yes Continue',
            handler: data => {
              var loading = this.loadingCtrl.create({
                spinner: 'bubbles',
                dismissOnPageChange: true,
                content: 'Uploading data is in progress <br> Please Wait...'
              });
             loading.present();

             try {
             
     const result = this.afAuth.auth.createUserWithEmailAndPassword(user.email, user.phone);
     if(result)
          this.afAuth.authState.take(1).subscribe(auth =>{
            this.userid = auth.uid;
 
            this.db2.object('photoalbum/users/'+auth.uid).set(user)
            .then(()=>{
             this.afAuth.auth.signOut().then(()=>{
 
               let alert2 = this.alertCtrl.create({
                 
                 title: '<font color="green"><b> </b>Upload Successful Your unique ID = <font color="black">'+this.userid+'</font></font>',
                 cssClass: "alert",
                 buttons: [
                   {
                     text: 'Ok',
                     role: 'cancel',
                     handler: data => {
                     //  this.afAuth.auth.signOut().then(()=>{})
                     }
                   },
                  
                 ]
               });
               alert2.present();
               loading.dismiss();
           
   
   
   
 
 
             });
              
             
            });
  
          });
                  //end db.authstarte
              //  this.itemsRef.push({ name: user.name, email : user.email, admno: user.admno, department: user.department });
              //  this.afAuth.auth.signInWithEmailAndPassword(user.email, user.password);
             //   var  userg = firebase.auth().currentUser;
              
             
                
          } catch(error){ 
               this.emailError = error.message;
               alert(error.message);
               
              console.error(error);
              
             }
          
          
           //end of register 
          










             
              
            
  
  
              }
          }
        ]
      });
      alert1.present();
  





      
      
    //   alert('No images ected');
   //    loading.present();
     
   
 
    }//end of else
   }//end of method
  



   error(){
    let alert = this.alertCtrl.create({
      title: '<font color="green"><b> </b> Full Name, State, Email, LGA, PhoneNo & Address are not optional  </font>',
      cssClass: "alert",
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          handler: data => {

          }
        },
       
      ]
    });
    alert.present();




   }//end of error

  
  removeFile(){
    this.img1 = "";
    this.selectedFile = "";
    /*
  \
    }
  
  */
   
  }



  upload(user){
    console.log(user);
  }



  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }
}



