import { Component ,OnInit, Inject } from '@angular/core';
import { IonicPage, MenuController, NavController, ModalController, NavParams, ToastController, ViewController, } from 'ionic-angular';

import { AlertController } from 'ionic-angular';

import { Storage } from '@ionic/storage';



@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  errMess: string;

  constructor(private alertCtrl: AlertController,private storage: Storage,public navCtrl: NavController, public navParams: NavParams, public menuCtrl :MenuController,
  
    public modalCtrl: ModalController,
    private toastCtrl: ToastController,
     ) {

      
       }
  
  ngOnInit() {
  
  }
  ionViewDidEnter() {
    // the root left menu should be disabled on this page
    this.menuCtrl.enable(true);
    }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MenuPage');
  //  this.presentPrompt();
  }


  presentPrompt() {
    let alert = this.alertCtrl.create({
      title: 'Login',
      cssClass: "alertb",
      inputs: [
        {
          name: 'username',
          placeholder: 'Username'
        },
        {
          name: 'password',
          placeholder: 'Password',
          type: 'password'
        }
      ],
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: data => {
            this.presentPrompt();
            console.log('Cancel clicked');
          }
        },
        {
          text: 'Login',
          handler: data => {
           
          }
        }
      ]
    });
    alert.present();
  }

 


 
  


}



